.node{
    width: 25px;
    height: 25px;
    outline: 1px solid gray;
    display: inline-block;
}

.node-end{
    background-color: rgb(232, 123, 123);
}

.node-start{
    background-color: rgb(130, 215, 130);
}

.node-wall{
    background-color: black;
}

.node-visited {
    animation-name: visitedAnimation;
    animation-duration: 1.5s;
    animation-timing-function: ease-out;
    animation-delay: 0;
    animation-direction: alternate;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
    animation-play-state: running;
  }

  @keyframes visitedAnimation {
    0% {
      transform: scale(0.3);
      background-color: rgba(66, 0, 41, 0.75);
      border-radius: 100%;
    }

    50% {
      background-color: rgba(165, 112, 173, 0.75);
    }

    75% {
      transform: scale(1.2);
      background-color: rgba(232, 166, 166, 0.7);
    }

    100% {
      transform: scale(1);
      background-color: rgba(213, 185, 255, 0.75);
    }
  }

  .node-shortest-path {
    animation-name: shortestPath;
    animation-duration: 1.5s;
    animation-timing-function: ease-out;
    animation-delay: 0;
    animation-direction: alternate;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
    animation-play-state: running;
  }

  @keyframes shortestPath {
    0% {
      transform: scale(0.6);
      background-color: rgb(255, 254, 106);
    }

    50% {
      transform: scale(1.2);
      background-color: rgb(255, 254, 106);
    }

    100% {
      transform: scale(1);
      background-color: rgb(255, 254, 106);
    }
  }